.app {
  text-align: center;
  padding: 30px;
  background: #f2f6ff;
  min-height: 100vh;
}

input {
  padding: 10px;
  margin: 10px;
  width: 250px;
}

.form {
  margin: 20px 0;
}

button {
  padding: 10px 15px;
  background: #4caf50;
  border: none;
  color: white;
  border-radius: 6px;
  cursor: pointer;
}

.book-list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
